import React, { useEffect ,useState} from 'react';
import './Sidebar.css';
import ChatBubbleIcon from "@mui/icons-material/Chat";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import FilterTiltShiftIcon from '@mui/icons-material/FilterTiltShift';
import SearchIcon from '@mui/icons-material/Search';
import{ Avatar, IconButton} from "@mui/material"
import SidebarChat from './SidebarChat';
import axios from 'axios';
import { useStateValue } from '../StateProvider';

const Sidebar = () => {
  const [rooms, setRooms] = useState([]);
  const [{ user }, dispatch] = useStateValue(); //  CORRETTO

  useEffect(() => {
    axios.get("http://127.0.0.1:9000/api/v1/rooms/sync").then((response) => {
      setRooms(response.data);
    });
  }, []);
  
   const createChat = async() => {
    const roomName = prompt("Inserisci un nome per la Chat !")
    if(roomName){
      await axios.post("http://127.0.0.1:9000/api/v1/rooms",{
        name:roomName
      }).then((response)=>{
        setRooms([...rooms,response.data])
      })
      
    }
  };
    return(
        <div className="sidebar">
            <div className="sidebar_header">
                <div className="sidebar_header_left">

                  <IconButton>

                    <Avatar src='https://assets.gazzettadelsud.it/2019/01/Adrian-4.jpeg'></Avatar>   
                    </IconButton>
                    {user?.displayName}
                </div>
             <div className="sidebar_header_right">
               
                <IconButton>
                <FilterTiltShiftIcon></FilterTiltShiftIcon>
                </IconButton>
                  <IconButton>
                <ChatBubbleIcon></ChatBubbleIcon>
                </IconButton>
                  <IconButton>
                <MoreVertIcon></MoreVertIcon>
                </IconButton>
             </div>
            </div>
            <div className= "sidebar_search">
              <div className="sidebar_search_container" >
                <SearchIcon></SearchIcon>
               <input type="text"  placeholder='Cerca o inizia una nuova chat'></input>
              </div>

            </div>
            
         <div className="sidebar_chats">
  <div onClick={createChat} className="sidebarChat">
    <h3>Add New Chat</h3>
  </div>

  {rooms.map((room, index) => (
    <SidebarChat
      key={index}
      id={room._id}
      name={room.name}
    />
  ))}
</div>
</div>
 );
};


export default Sidebar;