 import React, { useState, useEffect } from 'react';
import { InsertEmoticon } from "@mui/icons-material";
import "./Chat.css";
import { Avatar, IconButton } from '@mui/material';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';
import { useStateValue } from '../StateProvider';

function Chat({ messages }) {
  const { roomId } = useParams();
  const [input, setInput] = useState("");
  const [roomName, setRoomName] = useState("");
  const [lastSeen, setLastSeen] = useState("");
  const navigate = useNavigate();
  const [{ user }, dispatch] = useStateValue();

  useEffect(() => {
    if (roomId) {
      axios.get(`http://127.0.0.1:9000/api/v1/rooms/${roomId}`)
        .then((response) => {
          const room = response.data.room;
          setRoomName(room?.name || "Chat");

          const lastMessage = messages[messages.length - 1];
          setLastSeen(lastMessage?.timestamp);
        })
        .catch((error) => {
          console.error("Errore nel recuperare la stanza:", error);
          navigate("/");
        });
    }
  }, [roomId, messages, navigate]);

  const sendMessage = async (e) => {
    e.preventDefault();
    try {
      await axios.post("http://127.0.0.1:9000/api/v1/messages", {
        message: input,
        name: user?.displayName || "Anonimo",
        timestamp: new Date().toISOString(),
        uid: user?.uid || "default",
      });
      setInput("");
    } catch (error) {
      console.error("Errore nell'invio del messaggio:", error);
    }
  };

  return (
    <div className='Chat'>
      <div className='Chat_header'>
        <Avatar />
        <div className='Chat_header_info'>
          <h3>{roomName}</h3>
          <p>
            Visto l'ultima volta:{" "}
            {lastSeen && !isNaN(new Date(lastSeen))
              ? new Date(lastSeen).toLocaleString("it-IT", {
                  day: "2-digit",
                  month: "2-digit",
                  year: "numeric",
                  hour: "2-digit",
                  minute: "2-digit",
                  second: "2-digit",
                })
              : "Mai"}
          </p>
        </div>
      </div>

      <div className="Chat_body">
        {messages.map((message, index) => {
          const date = new Date(message.timestamp);
          const isValidDate = !isNaN(date);

          return (
            <div
              key={index}
              className={`Chat_message_container ${message.uid === user?.uid ? "Chat_receiver_container" : ""}`}
            >
              <span className="Chat_name_label">{message.name}</span>
              <div className={`Chat_message ${message.uid === user?.uid ? "Chat_receiver" : ""}`}>
                {message.message}
                <span className="Chat_timestamp">
                  {isValidDate
                    ? date.toLocaleString("it-IT", {
                        day: "2-digit",
                        month: "2-digit",
                        year: "numeric",
                        hour: "2-digit",
                        minute: "2-digit",
                        second: "2-digit"
                      })
                    : "Data non valida"}
                </span>
              </div>
            </div>
          );
        })}
      </div>

      <div className='Chat_footer'>
        <IconButton>
          <InsertEmoticon />
        </IconButton>
        <form onSubmit={sendMessage}>
          <input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Scrivi un messaggio..."
            type="text"
          />
          <button type="submit">Invia</button>
        </form>
      </div>
    </div>
  );
}

export default Chat;
